body {
  color: #fff;
  background-color: #111;
  margin: 0;
  padding: 0;
  font-family: monospace;
  overflow: hidden;
}

#scene-container {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.text-label-overlay {
  pointer-events: none;
  color: #fff;
  text-shadow: 1px 1px 2px #000;
  width: 100%;
  height: 100%;
  font-family: monospace;
  font-size: 14px;
  position: absolute;
  top: 0;
  left: 0;
}

.label-group {
  background-color: #0003;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  padding: 4px 8px;
  line-height: 1.4;
  display: flex;
  position: absolute;
  transform: translate(-50%);
}
