body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #111;
  color: white;
  font-family: monospace;
}
#scene-container {
  width: 100vw;
  height: 100vh;
  position: relative;
}
/* Styles for the 2D text labels */
.text-label-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  font-family: monospace;
  color: white;
  font-size: 14px;
  text-shadow: 1px 1px 2px black;
}
.label-group {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 1.4;
  transform: translate(-50%, 0);
  background-color: rgba(0, 0, 0, 0.2);
  padding: 4px 8px;
  border-radius: 4px;
}
